ul {
  list-style: none;
}
.gallery {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  gap: 30px;
}

.photo-card {
  width: 400px;
}

.load-more-btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.is-hidden {
  opacity: 0;
}

.visiable {
  opacity: 1;
}
